import { useEffect, useState } from "react"
import { Helmet } from "react-helmet"
import { userDownloadInvoice, userGetCart, userLoginVerify, userOrderList } from "services/Userservice"
import { Link, useNavigate } from "react-router-dom"
import SnackbarAlert from "custom/components/SnackbarAlert"
import { IPropsError, IPropsOrders, IPropsProductList, IPropsQTP, IPropsUserData } from "../../Interface";
import { AxiosError } from "axios"
import { Skeleton } from "@mui/material"
import { MobileMenu } from "components/MobileMenu/MobileMenu"
import fileDownload from "js-file-download"

const Uorderlist = () => {
  const nav = useNavigate()
  const [orderList, setOrderList] = useState<IPropsOrders[]>([])
  const [orderListError, setOrderListError] = useState<string>("")
  const [snackopen, setSnackOpen] = useState<IPropsError>({ open: false, severity: undefined, message: "" })
  const [orderCartData, setOrderCartData] = useState<IPropsProductList[]>([])
  const [userData, setUserData] = useState<IPropsUserData>({} as IPropsUserData)

  useEffect(() => {
    (async function () {
      if (localStorage.getItem("usertoken")) {
        try {
          const verify = await userLoginVerify(String(localStorage.getItem("usertoken")));
          if (verify.data.status === "Success") {
            setUserData(verify.data.userData)
            const { _id }: IPropsUserData = verify.data.userData
            const resOrderList = await userOrderList(_id)
            if (resOrderList.data.status === "Success") {
              setOrderList(resOrderList.data.orderList)
            } else {
              setOrderListError(resOrderList.data.message)
            }
            const getCart = await userGetCart(_id);
            if (getCart.data.status === "Success") {
              let cart = getCart.data.cartData
              setOrderCartData(cart)
            }
          } else {
            nav("/user/login")
          }
        } catch (error: unknown) {
          setSnackOpen({ open: true, severity: "warning", message: (error as AxiosError).message })
        }
      } else {
        nav("/user/login")
      }
    })();
  }, [nav])

  const getInvoice = async (orderData: IPropsOrders) => {
    try {
      const resInvoice = await userDownloadInvoice(orderData)
      if (resInvoice.data.status !== "Failed") {
        const url = window.URL.createObjectURL(new Blob([resInvoice.data]));
        const a = document.createElement('a');
        a.href = url;
        a.download = 'invoice.pdf';
        document.body.appendChild(a);
        a.click();
        document.body.removeChild(a);
      } else {
        setSnackOpen({ open: true, severity: "error", message: resInvoice.data.message })
      }
    } catch (error: unknown) {
      setSnackOpen({ open: true, severity: "warning", message: (error as AxiosError).message })
    }
  }

  return (
    <>
      <Helmet>
        <title>Order List</title>
        <link rel="stylesheet" href="/assets/css/OrderList.css" />
      </Helmet>
      <section id="wrapper">
        <header className="shadow ">
          <div className="container">
            <div className="d-flex flex-wrap align-items-center justify-content-between justify-content-lg-between">
              <div className="col-md-5">
                <h1 className="cartTitle">
                  <Link to="/user/dashboard/cart"
                    className="d-flex align-items-center mb-2 mb-lg-0 text-dark text-decoration-none">
                    <span><i className="fa-solid fa-arrow-left"></i> Orders</span>
                  </Link>
                </h1>
              </div>
              <div className="mobiBar">
                <button className="btn btn-primary" onClick={() => nav('/user/dashboard/products')}>
                  <i className="fa-regular fa-compass"></i> Explore
                </button>
                <button id="userProf" className="transBtn" onClick={() => nav("/user/dashboard/profile")}>
                  <i className="fa-regular fa-user"></i>
                  <span>Profile</span>
                </button>
                <button id="ordersMenu" className="transBtn" >
                  <i className="fa-regular fa-file-lines"></i>
                  <span>Orders</span>
                </button>
                <button id="cartAdd" className="transBtn" onClick={() => nav("/user/dashboard/cart")}>
                  <b>{orderCartData.length}</b>
                  <i className="fa-solid fa-cart-shopping"></i>
                  <span>Cart</span>
                </button>
                <div className="dropdown show">
                  <button id="toggleMenu dropdown-toggle" className="transBtn" data-toggle="dropdown"
                    data-bs-toggle="dropdown" aria-expanded="false">
                    <i className="fa-solid fa-bars"></i>
                  </button>

                  <div className="dropdown-menu" aria-labelledby="optBar" role="menu">
                    <MobileMenu userData={userData} />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </header>

        <section className="myorder-section mt-5 mb-3">
          <div className="container">
            <div className="row  justify-content-center">
              <div className="col-lg-8 col-sm-12">
                <div className="d-flex justify-content-between align-items-center py-3">
                  <h2 className="h5 mb-0"><Link to="#" className="text-muted"></Link> My Orders</h2>
                </div>
                <div className="tab-content" id="myTabContent">
                  <div className="tab-pane fade active show" id="orders" role="tabpanel" aria-labelledby="orders-tab">
                    {
                      orderList.length ?
                        orderList.map((order: IPropsOrders, i: number) => {
                          return (
                            <div key={i} className="bg-white card mb-4 order-list shadow-sm">
                              <div className="gold-members p-4">
                                <div className="media">
                                  <div className="media-body">
                                    <div>
                                      <span className="float-right text-warning mb-2">{order.deliveryStatus} on {order.deliveryStatus === "Ordered" ? convertDate(order.createdAt) : convertDate(order.updatedAt)}</span>
                                    </div>
                                    <h6 className="mb-2">
                                      <div className="text-black text-gray mb-3"><i
                                        className="fa fa-truck mr-2" aria-hidden="true"></i>
                                        ORDER {order._id}</div>
                                    </h6>
                                    <p className="mt-3 mb-2">
                                      <b>Address:</b>
                                    </p>
                                    <p className="text-gray mb-1">
                                      <i className="icofont-location-arrow"></i>
                                      <address>
                                        <strong>{order.shippingAddress.fullName}</strong><br />
                                        {order.shippingAddress.houseNoOrBuildingName} {order.shippingAddress.areaOrColony}, <br />
                                        {order.shippingAddress.city}, {order.shippingAddress.country} {order.shippingAddress.zipOrPostalCode}<br />
                                        <abbr title="Phone">P:</abbr> {order.shippingAddress.phoneNumber}
                                      </address>
                                    </p>

                                    <p className="mt-3 mb-2">
                                      <b>Products:</b>
                                    </p>
                                    <p className="text-dark">
                                      {
                                        order.orderedProducts.map((op: IPropsProductList) => {
                                          return op.quantityAndTypeAndPrice.map((qtp: IPropsQTP) => {
                                            return (
                                              <div key={op._id}>{op.productName}: {qtp.price} x {qtp.userQuantity} = {
                                                op.quantityAndTypeAndPrice.reduce((stotal: number, qtp: IPropsQTP) => {
                                                  return stotal + (qtp.userQuantity as number) * qtp.price
                                                }, 0)
                                              }</div>
                                            )
                                          })
                                        })
                                      }
                                    </p>
                                    <hr />
                                    <div className="float-right">
                                      <button className="btn btn-sm btn-outline-primary" onClick={() => getInvoice(order)}><i
                                        className="icofont-headphone-alt"></i>
                                        Get Invoice</button>
                                      <Link className="btn btn-sm btn-primary" to={`/user/dashboard/order/${order._id}`}><i
                                        className="icofont-refresh"></i>
                                        View Details</Link>
                                    </div>
                                    <p className="mb-0 text-black text-primary pt-2"><span
                                      className="text-black font-weight-bold">
                                      Total Paid:</span> {
                                        order.orderedProducts.reduce((total: number, ocd: IPropsProductList) => {
                                          return total + ocd.quantityAndTypeAndPrice.reduce((stotal: number, qtp: IPropsQTP) => {
                                            return stotal + (qtp.userQuantity as number) * qtp.price
                                          }, 0)
                                        }, 0)
                                      }
                                    </p>
                                  </div>
                                </div>
                              </div>
                            </div>
                          )
                        })
                        :
                        orderListError ?
                          <div>{orderListError}</div>
                          :
                          Array(3).fill(0).map((_, i: number) => {
                            return (
                              <div key={i} className="bg-white card mb-4 order-list shadow-sm">
                                <div className="gold-members p-4">
                                  <div className="media">
                                    <div className="media-body">
                                      <div>
                                        <Skeleton animation="wave" variant="rounded" />
                                      </div>
                                      <h6 className="m-2">
                                        <Skeleton animation="wave" variant="rounded" />
                                      </h6>
                                      <p className="mt-3 mb-2">
                                        <Skeleton animation="wave" variant="rounded" />
                                      </p>
                                      <p className="text-gray mb-1">
                                        <i className="icofont-location-arrow"></i>
                                        <address>
                                          <Skeleton animation="wave" variant="rounded" height={60} />
                                        </address>
                                      </p>

                                      <p className="mt-3 mb-2">
                                        <Skeleton animation="wave" variant="rounded" />
                                      </p>
                                      <p className="text-dark">
                                        <Skeleton animation="wave" variant="rounded" />
                                      </p>
                                      <hr />
                                      <div className="float-right">
                                        <Skeleton animation="wave" variant="rounded" />
                                      </div>
                                      <p className="mb-0 text-black text-primary pt-2"><Skeleton animation="wave" variant="rounded" /></p>
                                    </div>
                                  </div>
                                </div>
                              </div>
                            )
                          })
                    }
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>

        <section id="copyrights">
          <div className="container">
            <div className="copywrap">
              <p>&copy; 2023 <Link to="https://www.pmgsuperstore.com">pmgsuperstore.com</Link>. All rights reserved
              </p>
              <ul>
                <li><Link to="#">Privacy Policy</Link></li>
                <li><Link to="#">Terms of Use</Link></li>
              </ul>
            </div>
          </div>
        </section>
      </section>
      <SnackbarAlert snackopen={snackopen} setSnackOpen={setSnackOpen} />
    </>
  )
}

function convertDate(dateData: string | Date) {
  let date: Date = new Date(dateData);
  const options: Intl.DateTimeFormatOptions = {
    weekday: 'short',
    month: 'short',
    day: 'numeric',
    hour: '2-digit',
    minute: '2-digit',
  };
  return new Intl.DateTimeFormat('en-US', options).format(date);
}

export default Uorderlist;